%hamburger{
  background-color:aliceblue;
  width: 100%;
  height: 5px;
  border-radius: 1px;
  transition: all 0.5s ease;
}

.menu-container{
  width: 50px;
  height: 50px;
  border: 3px solid aliceblue;
  margin: 30px auto 0 auto;
  position: relative;
  transition: all 0.5s ease;

  &__text{
    position: absolute;
    bottom: 0;
    left: 2.5px;
    color:aliceblue;
  }

  & span{
    @extend %hamburger;
    display: block;
    position: relative;
    left: 0;
    top:15px;

    &::before{
      @extend %hamburger;
      content:"";
      position:absolute;
      top: -10px;
    }

    &::after{
      @extend %hamburger;
      content:"";
      position:absolute;
      top: 10px;
    }
  }
  &:hover{
    cursor: pointer;
    border-radius: 20%;

    & span{
      background-color: transparent;
    }

  }

}

// .menu-container span,
// .menu-container span::before,
// .menu-container span::after


.menu-container:hover{
  cursor: pointer;
  border-radius: 20%;
  /* border-color:rgb(255, 241, 51); */
}

.menu-container:hover span{
  background-color: transparent;
}
.menu-container:hover span::before{
  transform: rotate(45deg) translate(6.5px,6px);
  /* background-color: rgb(255, 241, 51); */

}
.menu-container:hover span::after{
  transform: rotate(-45deg)translate(6px,-9px);
  /* background-color: rgb(255, 241, 51); */

}
.menu-container:hover p{
  color: rgb(255, 241, 51);
}
